<template>
  <div class="">
    <el-row class="text_align_left white_bg">
      <p class="advice_title text_align_left padding_left10">{{articleTitle}}</p>
      <el-row :gutter="20" class="text_align_left padding_left10">
        <el-col :span="6">
          <img src="../assets/healthAdvice/shijian@2x.png" height="16" width="16" class="vertical_align"/>
          <span class="date_font">{{publishedTime}}</span>
        </el-col>
        <el-col :span="3">
          <img class="vertical_align" src="../assets/healthAdvice/yanjing@2x.png" height="16" width="16"/>
          <span class="date_font">{{articleViews}}</span>
        </el-col>
      </el-row>
      <hr align="center" style="border: 1px dashed #CCCCCC;" />
      <el-row>
        <el-col :span="24" class="text_align_left padding_left10">
          <img :src="articleCovers" width="" height="300px"/>
        </el-col>
      </el-row>
      <p class="article_content text_align_left padding_left10">{{articleContent}}</p>
    </el-row>

  </div>
</template>

<script>
  import Verify from '../utils/utils'
  import {mapGetters} from 'vuex'

  export default {
    data() {
      return {
        articleTitle:'',//文章标题
        articleCovers:'',//文章封面
        articleContent:'',//文章内容
        publishedTime:'',//这里取的最后一次修改时间
        articleViews:'',//文章浏览量
      }
    },
    created() {
      this.getArticleDetail();
      // console.log(articleId);
    },
    computed: {
      ...mapGetters(['articleId'])

    },
    methods: {
      getArticleDetail() {
        let _self = this;
        // let url = '/api/content/articles/'+this.$route.query.id;
        // debugger
        let url = '/api/content/articles/'+this.articleId;
        _self.$get(url, {
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("文章详情==" + JSON.stringify(res.data.data));
            _self.articleTitle = res.data.data.title;
            if (res.data.data.File !=null) {
              _self.articleCovers = "http://"+res.data.data.File.url;
            }
            _self.publishedTime = Verify.convertDate(res.data.data.updatedAt);
            _self.articleViews = res.data.data.pageviews;
            _self.articleContent = res.data.data.summary;//content
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
    }
  }
</script>

<style>
  @import "../styles/personal/personal-center.scss";
  .white_bg{
    background-color: white;
  }
  /*.padding_left_right {*/
    /*padding-left: 10%;*/
    /*padding-right: 10%;*/
  /*}*/

  .advice_title {
    font-size: 18px;
    font-weight: bold;
    color: #404044;
  }

  .date_font {
    color: #CCCCCC;
    font-size: 12px;
  }

  .vertical_align {
    vertical-align: middle;
  }

  .article_content {
    font-size: 12px;
    color: #333333;
    line-height: 24px;
  }

  /*文章分割线*/
  .separate {
    height: 2px;
    background-color: #e6e6e6;
    border: none;
  }
</style>



