<template>
  <div>
    <el-row>
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <el-carousel height="367px">
          <el-carousel-item v-for="(img,index) in imgList" :key="index" class="img">
            <img v-bind:src="img.url" class="img">
          </el-carousel-item>
        </el-carousel>
      </el-col>
    </el-row>
    <!--推荐套餐-->
    <health-checkup></health-checkup>
    <cooper-institution></cooper-institution>
    <serve-client></serve-client>
  </div>

</template>

<script>
  import HealthCheckup from "../../components/HealthCheckup"
  import CooperativeInstitution from "../../components/CooperativeInstitution"
  import ServeClient from "../../components/ServeClients"
    export default {
      name:'homePage',
      components:{
        'health-checkup':HealthCheckup,
        'cooper-institution':CooperativeInstitution,
        ServeClient
        },
        data() {
            return {
              imgList:[
                {url:require('../../assets/banner_index@2x.png')},
                {url:require('../../assets/banner_index@2x.png')}
              ]
            }
        },
      created(){
        // this.getProductList();
      },
      methods:{
        getProductList(){
          // let userId = localStorage.getItem("userId");
          let product = '/api/user/products';
          this.$get(product, {}).then(res => {
            if (JSON.stringify(res.data.code) == 200) {
              debugger
              // this.inviteNum = res.data.data.inviteBasic.inviteNumber;
              // this.inviteBasicInfo = res.data.data.inviteBasic.items;
              console.log("test" + JSON.stringify(res.data.data));
            } else {
              alert(JSON.stringify(res.data.msg));
            }
          }).catch(function (reason) {
            console.log('catch:', reason);
          });
        }
      }
    }
</script>

<style>
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .img{
    /*margin:0 auto;*/
    height: 367px;
    width: 100%;
  }
</style>



