<template>
  <div class="padding_left_right">
    <el-header>
      <el-row>
        <el-col :span="10">
          <div class="logo_img_div">
            <img src="../../assets/logo_login@2x.png" class="logo_img"/>
          </div>
        </el-col>
        <el-col :span="12" class="margin_top15">
          <el-steps :active="active" align-center>
            <el-step title="我的购物车" description=""></el-step>
            <el-step title="确认订单" description=""></el-step>
            <el-step title="支付" description=""></el-step>
            <el-step title="支付成功" description=""></el-step>
          </el-steps>
          <!--<el-button style="margin-top: 12px;" @click="next()">下一步</el-button>-->
        </el-col>
      </el-row>


    </el-header>
    <div v-if="cartParams === 'order'">
      <div>
        <p class="orderTitle">收货地址</p>
        <div class="padding_left10 padding_right10">
          <el-row :gutter="20">
            <el-col :span="8" v-for="(o, index) in 4" :key="o" :class="{'marginTop15':index>2}">
              <el-card :body-style="{ padding: '0px' }" shadow="never" class="text_align_left padding_left10">
                <div slot="header" class="clearfix">
                  <span>{{consignee}}</span>
                  <span> &emsp;{{city}}</span>
                  <el-button style="float: right; padding: 3px 0" type="text">家里</el-button>
                </div>
                <div class="card_content">
                  <p class="district">{{addressDistrict}}</p>
                  <p class="district">{{detailAddress}}</p>
                  <p class="district margin_bottom15">{{tel}}</p>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>
      </div>
      <div>
        <p class="orderTitle">支付方式</p>
        <div class="padding_left10 padding_right10">
          <el-row :gutter="20">
            <el-col :span="4">
              <el-card shadow="never" class="text_align_left padding_left10">
                <div class="card_content">
                  <el-radio v-model="radio" class="district" label="WeChatPayment" @change="radioChange()"><img class="pay_img"
                                                                                                                src="../../assets/order/zz_ddlc_qrdd@2x.png"
                                                                                                                height="34" width="34"/>微信支付
                  </el-radio>
                </div>
              </el-card>
            </el-col>
            <el-col :span="4">
              <el-card shadow="never" class="text_align_left padding_left10">
                <div class="card_content">
                  <el-radio v-model="radio" class="district" label="TransferPayment" @change="radioChange()"><img class="pay_img"
                                                                                                                  src="../../assets/order/zz_ddlc_qrdd@2x.png"
                                                                                                                  height="34" width="34"/>公司转账
                  </el-radio>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>
      </div>
      <div>
        <p class="orderTitle">商品及服务信息</p>
        <div class="padding_left10 padding_right10">
          <el-table
            ref="multipleTable"
            :data="orderData"
            tooltip-effect="dark"
            style="width: 100%">
            <el-table-column label="商品" width="100">
              <template slot-scope="scope">
                <img :src="scope.row.pic" width="40" height="40" class="head_pic"/>
              </template>
            </el-table-column>
            <el-table-column
              label="商品名称"
              width="120">
              <template slot-scope="scope">{{ scope.row.date }}</template>
            </el-table-column>
            <el-table-column
              prop="name"
              label="单价"
              width="120">
            </el-table-column>
            <el-table-column
              prop="number"
              label="数量"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              prop="name"
              label="小计"
              width="120">
            </el-table-column>
          </el-table>
        </div>
        <div>
          <p class="coupon">优惠券</p>
          <div class="padding_left10 padding_right10">
            <el-row :gutter="20">
              <el-col :span="4" v-for="(o, index) in 3" :key="o" :class="{'marginTop15':index>2}">
                <el-card :body-style="{ padding: '0px' }" shadow="never" class="text_align_left padding_left10">
                  <div class="card_content">
                    <div class="effective_coupon_img">¥30</div>
                    <p class="district">{{couponIntroduce}}</p>
                    <p class="district margin_bottom15">{{effectiveDate}}</p>
                  </div>
                </el-card>
              </el-col>
              <el-col :span="12" class="text_align_right padding_right20">
                <p class="pay_list_content">运费&emsp;¥{{freight}}</p>
                <p class="pay_list_content">优惠&emsp;¥{{freight}}</p>
                <p class="pay_list_content">优惠券&emsp;¥{{freight}}</p>
                <p class="pay_list_content"><span class="quantity">{{goodsQuantity}}</span>件商品，总额&emsp;¥{{sumPrice}}</p>
              </el-col>
            </el-row>
          </div>
          <div class="text_align_right padding_right20">
            <p class="pay_list_content">帐户可用额度<span class="quantity">¥{{limitMoney}}</span>,抵扣<span class="quantity">¥{{deductionMoney}}</span></p>
            <p class="pay_list_title">收货地址:<span class="pay_list_content">{{receiptAddress}}</span></p>
            <p class="pay_list_title">收货人:<span class="pay_list_content">{{selectConsignee}}</span></p>
            <p class="pay_list_title">应付金额:<span class="payable">¥{{AmountPayable}}</span></p>
            <el-button class="commit_order_btn" @click="commitOrderEvent()">提交订单</el-button>
          </div>
        </div>

      </div>
    </div>
    <!--<div v-else-if="cartParams === 'payment'">-->
      <!--<compent v-bind:is="cartParams === 'payment'">-->
        <!--<order-payment></order-payment>-->
      <!--</compent>-->

    <!--</div>-->
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import OrderPayment from "../cart/OrderPayment"
  export default {
    name: "ConfirmOrder",
    components:{
      OrderPayment,
    },
    data() {
      return {
        active:1,
        city: '北京 北京市',
        consignee: '****',
        addressDistrict: '昌平区 回龙观',
        detailAddress: 'XX小区XXX号楼603',
        tel: '130****1234',
        radio: 'TransferPayment',//TransferPayment
        orderData: [{
          pic: require("@/assets/firm/fenzu_icon_d@2x.png"),
          date: '2016-05-03',
          name: '王小虎',
          number: 20
        }, {
          pic: require("@/assets/firm/fenzu_icon_d@2x.png"),
          date: '2016-05-02',
          name: '王小虎',
          number: 20
        }, {
          pic: require("@/assets/firm/fenzu_icon_d@2x.png"),
          date: '2016-05-04',
          name: '王小虎',
          number: 20
        }, {
          pic: require("@/assets/firm/fenzu_icon_d@2x.png"),
          date: '2016-05-07',
          name: '王小虎',
          number: 20
        }],
        couponIntroduce: '¥30[满100减30]全场通用',
        effectiveDate: '2018-12-12到2019-12-12',
        goodsQuantity: 1,//商品件数
        freight: '0.00',//运费
        sumPrice: '2300.00',
        limitMoney: '1500.00',//账户余额
        deductionMoney: '1200.00',//抵扣金额
        receiptAddress: '北京 北京市 海淀区XXX小区XX',
        selectConsignee: '刘XXX',//收货人
        AmountPayable: '1500.00'//应付金额
      }
    },
    computed: {
      ...mapGetters(['cartParams'])
    },
    created(){
      this.$store.dispatch('setPaymentMethod',this.radio);
    },
    methods:{
      commitOrderEvent(){
        this.$store.dispatch('setCartParams', 'payment');
        // this.$router.push({ path: '/OrderPayment'});
      },
      radioChange(){
        this.$store.dispatch('setPaymentMethod',this.radio);
        console.log("123==="+this.radio);
      },
      next() {
        if (this.active++ > 2) this.active = 0;
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/personal/personal-center.scss";

  .logo_img_div {
    text-align: left;
  }

  .logo_img {
    text-align: left;
    height: 60px;
    margin-top: 0.75rem;
  }

  .orderTitle {
    font-size: 18px;
    font-weight: bold;
    color: #333333;
    padding-top: 10px;
    padding-left: 10px;
    text-align: left;
  }

  .marginTop15 {
    margin-top: 15px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both;
  }

  .pay_img {
    vertical-align: middle;
  }

  /*'区'的字体样式*/
  .district {
    font-size: 16px;
    color: #000000;
  }

  /*优惠券标题*/
  .coupon {
    font-size: 14px;
    color: #404040;
    padding-left: 10px;
    text-align: left;
  }

  /*优惠券的'块'*/
  .coupon_img {
    margin-top: 10px;
    width: 78px;
    height: 36px;
    background: #CCCCCC;
    color: #ffffff;
    font-size: 24px;
    text-align: center;
  }

  .effective_coupon_img {
    margin-top: 10px;
    width: 78px;
    height: 36px;
    background: #FA9992;
    color: #ffffff;
    font-size: 24px;
    text-align: center;
  }

  .quantity {
    color: #DF2321;
    font-size: 14px;
  }
  .pay_list_title{
    font-weight: bold;
    font-size: 14px;
    color: #404040;
  }
  .pay_list_content{
    font-weight: normal;
    font-size: 14px;
    color: #666666;
  }
  .payable{
    font-size: 24px;
    color: #DF2321;
  }
  .commit_order_btn{
    background-color:#DF2321;
    color: #ffffff;
    border-radius: 0px;
    width: 158px;
    height:46px;
  }
</style>



