<template>
  <div>
    <el-header class="header_bg">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>{{asideNavTitle[0]}}</el-breadcrumb-item>
        <el-breadcrumb-item>{{asideNavTitle[1]}}</el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <div class="firm_detail margin_top15">
      <el-row :gutter="10">
        <el-col :span="6">
          <p class="number_font"><span class="number_people">{{outstandingOrder}}</span>个</p>
          <img class="balance_img" src="../../../assets/firm/dzfdd_fy_zhzl@2x.png" height="28" width="28"/>
          <span class="account_balance">待支付订单</span>
        </el-col>

        <el-col :span="9" class="left_box">
          <p class="balance_font">¥{{balance}}</p>
          <el-row>
            <el-col :span="16">
              <img class="balance_img" src="../../../assets/firm/zhanghuyue_icon@2x.png" height="28" width="28"/>
              <span class="account_balance">账户余额</span>
            </el-col>
            <el-col :span="8">
              <p class="top_up">充值</p>
            </el-col>
          </el-row>
        </el-col>

        <el-col :span="8" class="left_box">
          <p class="invoice_font">¥{{invoicing}}</p>
          <el-row>
            <el-col :span="16">
              <img class="balance_img" src="../../../assets/firm/fapiao_icon_green@2x.png" height="28" width="28"/>
              <span class="invoice">可开具发票</span>
            </el-col>
            <el-col :span="8">
              <p class="request_invoice">索要发票</p>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
    <div class="white_bg_color margin_top15">
      <el-row>
        <el-col :span="12" class="text_align_left padding_left10">
          <p class="account_balance">最近订单</p>
        </el-col>
        <el-col :span="12" class="text_align_right padding_right10">
          <p class="more_font">更多</p>
        </el-col>
      </el-row>
      <div class="padding_left10 padding_right10">
        <el-table
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange" :header-cell-style="tableHeaderColor" class="padding_left10 padding_right10">
          <el-table-column
            prop="name"
            label="订单号"
            width="120">
          </el-table-column>
          <el-table-column
            prop="tel"
            label="产品"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="depart"
            label="订单金额"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="depart"
            label="状态"
            show-overflow-tooltip>
            width="180">
            <template slot-scope="scope">
              <span class="special_color">{{scope.row.depart}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="suit"
            label="创建时间"
            show-overflow-tooltip>

          </el-table-column>
          <el-table-column
            prop="suit"
            label="支付时间"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            fixed="right"
            label="操作"
            width="180">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="text" size="small">支付详细</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex';
  import VerifyUtils from '../../../utils/utils';

  export default {
    name: "FirmPage",
    props: ['selectTitle'],
    data() {
      return {
        outstandingOrder: 0,
        balance: '0.00',
        invoicing: '0.00',
        staffNumber: '0',
        departNumber: '0',
        tableData: [{
          tel: '2016-05-03',
          name: '王小虎',
          depart: '策划部',
          suit: '800元'
        }]
      }
    },
    created() {
      this.getFirmAccountInfo();
      //待完善
      // this.getRecentOrderList();
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },
    methods: {
      //账户的订单、余额信息
      getFirmAccountInfo(){
        let enterpriseId = localStorage.getItem("enterpriseId");
        let firmAccountInfo = '/api/enterprise/account/'+ enterpriseId;
        this.$get(firmAccountInfo, {
          enterpriseId: enterpriseId,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            if (VerifyUtils.validateIsNull(res.data.data.orderBasic.waitPayNumber)) {
              this.outstandingOrder = res.data.data.orderBasic.waitPayNumber;
            }

            if (VerifyUtils.validateIsNull(res.data.data.invoiceBasic.money)) {
              this.invoicing = res.data.data.invoiceBasic.money;
            }

            if (VerifyUtils.validateIsNull(res.data.data.accountBasic.balance)) {
              this.balance = res.data.data.accountBasic.balance;
            }

            console.log("test" + JSON.stringify(res.data));
          } else {
            swal("提示",res.data.msg, "error");
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      //最新订单
      getRecentOrderList(){
        let enterpriseId = localStorage.getItem("enterpriseId");
        let firmAccountInfo = '/api/enterprise/order/'+ enterpriseId+'/lately';
        this.$get(firmAccountInfo, {
          enterpriseId: enterpriseId,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("order==" + JSON.stringify(res.data));
          } else {
            swal("提示",res.data.msg, "error");
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      handleSelectionChange(val) {

      },
      // 修改table header的背景色
      tableHeaderColor({ row, column, rowIndex, columnIndex }) {
        if (rowIndex === 0) {
          return 'background-color: #F6F6F6;color: #404040;font-weight: 500;'
        }
      }
    }
  }
</script>

<style scoped>
  @import "../../../styles/personal/personal-center.scss";

  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }

  .el-breadcrumb {
    font-size: 18px;
    line-height: 3.5;
    color: #333333;
  }

  .firm_detail {
    background-color: #FFFFFF;
    height: 124px;
  }

  /*分割线*/
  .left_box {
    margin-top: 5px;
    border-left: 1px solid #666666;
  }

  /*订单个数的数字样式*/
  .number_people {
    color: #009BDD;
    font-size: 30px;
  }

  /*个数的'个'字样式*/
  .number_font {
    color: #666666;
    font-size: 14px;
  }

  /*'更多'的字体样式*/
  .more_font {
    color: #009BDD;
    font-size: 14px;
  }

  /* 账户余额的字体*/
  .balance_font {
    color: #FA6A5A;
    font-size: 30px;
    margin-top: 14px;
    margin-bottom: 14px;
  }

  .balance_img {
    vertical-align: middle;
  }

  /*'充值'的字体样式*/
  .top_up {
    color: #FA6A5A;
    font-size: 14px;
    margin-top: 2px;
  }

  .account_balance {
    color: #404040;
    font-size: 14px;
  }

  /*发票相关样式*/
  .invoice_font {
    color: #5BC146;
    font-size: 30px;
    margin-top: 14px;
    margin-bottom: 14px;
  }

  .request_invoice {
    color: #5BC146;
    font-size: 14px;
    margin-top: 2px;
  }
</style>



