<template>
  <header>
    <el-header>
      <div class="logo_img_div">
        <img src="../assets/logo_login@2x.png" class="logo_img"/>
      </div>
    </el-header>
  </header>

</template>
<style>
  header{
    background-color: white;
  }
  .logo_img_div {
    margin-left: 6.75rem;
    text-align: left;
  }

  .logo_img {
    text-align: left;
    height: 60px;
    margin-top: 0.75rem;
  }
</style>
<script>
  export default {
    name: 'Header',
    data() {
      return {
      };
    }
  }
</script>




