<template>
  <div>
    <p class="float-left">当前绑定手机号：<span v-model="telNumber">{{telNumber}}</span></p>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm"
             class="input_width">
      <!--//input-width="305px"-->
      <el-form-item label="新手机号" prop="phoneNumber" label-width="90px">
        <el-input type="tel" v-model="ruleForm.phoneNumber" autocomplete="off"
                  placeholder=" 请输入手机号"></el-input>
      </el-form-item>
      <el-form-item label="图形验证码" prop="graphCode" label-width="90px">
        <el-row :gutter="10">
          <el-col :span="10">
            <el-input type="text" v-model="ruleForm.graphCode" autocomplete="off"
                      placeholder="请输入图形验证码"></el-input>
          </el-col>
          <el-col :span="8">
            <el-button class="img_code" @click="getImgCode()" v-show="isShow">获取图形码</el-button>
            <div v-html="svg" v-show="!isShow" class="img_code_height">
              {{svg}}
            </div>
          </el-col>
          <el-col :span="4">
            <span class="refresh_img_code" v-show="!isShow" @click="refreshImgCode()">换一换</span>
          </el-col>
        </el-row>

      </el-form-item>
      <el-form-item label="短信验证码" prop="messageCode" label-width="90px">
        <el-row :gutter="10" style="">
          <el-col :span="12">
            <el-input v-model="ruleForm.messageCode" placeholder="请输入短信验证码"></el-input>
          </el-col>
          <el-col :span="8">
            <el-button @click="getMessageCode()">短信验证码</el-button>
          </el-col>
          <el-col :span="4">

          </el-col>
        </el-row>

      </el-form-item>
      <el-form-item>
        <el-button class="login_btn" type="primary" @click="sureModifyNewTel()">确定</el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
  import VerifyUtils from '../../../utils/utils';
  import swal from 'sweetalert2'; //alert框

  export default {
    name: "ModifyBindTel",
    data() {
      return {
        telNumber: '',
        svg: '',
        showImgCode: '',
        isShow: false,
        ruleForm: {
          phoneNumber: '',
          graphCode: '',
          messageCode: '',
        },
        rules: {
          phoneNumber: [
            {validator: VerifyUtils.phoneNumberVerify, trigger: 'blur'}
          ],
          //图形码
          graphCode: [
            {validator: VerifyUtils.graphCodeVerify, trigger: 'blur'}
          ],
          //短信验证码
          messageCode: [
            {type: 'number', validator: VerifyUtils.messageCodeVerify, trigger: 'blur'}
          ],
        }
      }
    },
    created() {
      if (VerifyUtils.validateIsNull(localStorage.getItem("phoneNumber"))) {
        this.telNumber = localStorage.getItem("phoneNumber");
      }
      this.getImgCode();
    },
    methods: {
      getImgCode() {
        this.$get('/api/service/captcha', {
          width: '',
          height: '',
        }).then(res => {
          if (res.status == 200) {
            this.svg = res.data;
            this.isShow = false;
          } else {
            alert(res.statusText);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      refreshImgCode() {
        this.getImgCode();
      },
      getMessageCode() {
        this.$get('/api/service/sms', {
          captcha: this.ruleForm.graphCode,
          mobile: this.ruleForm.phoneNumber,
        }).then(res => {
          // console.log("test" + JSON.stringify(res));
          if (JSON.stringify(res.data.code) == 200) {
            this.$message({
              message: '已发送短信验证码，请查收',
              type: 'success'
            });
          } else {
            swal("提示", res.data.msg, "error");
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      //确认修改手机号
      sureModifyNewTel() {
        let userId = localStorage.getItem("userId");
        let modifyTelUrl = "/api/personal/" + userId + '/phoneReplacement';
        this.$put(modifyTelUrl, {
          mobile: this.ruleForm.phoneNumber,
          captcha: this.ruleForm.graphCode,
          // verifyCode: this.ruleForm.messageCode.toString(),
          smsCode: this.ruleForm.messageCode.toString(),
          // userId:userId,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            swal("提示", "修改成功", "success");
            localStorage.removeItem("phoneNumber");
            localStorage.setItem("phoneNumber", this.ruleForm.phoneNumber);
            // this.$router.go(0)
          } else {
            swal("提示", res.data.msg, "error");
          }
          // console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      }
    }
  }
</script>

<style scoped>
  .float-left {
    /*float: left;*/
  }

  .login_btn {
    width: 306px;
    height: 43px;
    background-color: #33B2E8;
    text-align: center;
    /*padding-left: 22px;*/
    /*padding-right: 22px;*/
  }

  .img_code_height {
    height: 40px;
  }

  .refresh_img_code {
    font-size: 10px;
    line-height: 12px;
    color: red;
  }

  .input_width {
    width: 460px;
  }
</style>



