<template>
  <div class="login_div">
    <login-header></login-header>
    <el-container>
      <el-main>
        <div class="bg_img">
          <el-tabs type="border-card" class="login_box">
            <div class="hind_login">未注册用户可直接通过手机号登录</div>
            <el-tab-pane label="手机快速登录">
              <el-form :model="loginRule" status-icon :rules="loginRules" ref="loginRule" input-width="305px"
                       class="demo-ruleForm">
                <el-form-item label="" prop="phoneNumber">
                  <el-input type="phone" v-model="loginRule.phoneNumber" autocomplete="off"
                            placeholder="输入手机号，快速登录"></el-input>
                </el-form-item>
                <el-form-item label="" prop="graphCode">
                  <el-row :gutter="10">
                    <el-col :span="12">
                      <el-input style="" type="text" v-model="loginRule.graphCode" autocomplete="off"
                                placeholder="请输入图形验证码"></el-input>
                    </el-col>
                    <el-col :span="8">
                      <el-button class="img_code" @click="getImgCode()" v-show="isShow">获取图形码</el-button>
                      <div v-html="svg" v-show="!isShow" class="img_code_height">
                        {{svg}}
                      </div>
                    </el-col>
                    <el-col :span="4">
                      <span class="refresh_img_code" v-show="!isShow" @click="refreshImgCode()">换一换</span>
                    </el-col>
                  </el-row>
                </el-form-item>
                <el-form-item label="" prop="messageCode">
                  <el-row :gutter="10">
                    <el-col :xs="14" :sm="14" :md="14" :lg="14" :xl="14">
                      <el-input v-model ="loginRule.messageCode" placeholder="请输入短信验证码"></el-input>
                    </el-col>
                    <el-col :xs="10" :sm="10" :md="10" :lg="10" :xl="10">
                      <el-button @click="getMessageCode()">短信验证码</el-button>
                    </el-col>
                  </el-row>
                </el-form-item>
                <el-form-item>
                  <el-button class="login_btn" type="primary" @click="submitForm()">登录</el-button>
                  <span class="forget_pwd" @click="forgetPwd()">忘记密码</span>
                </el-form-item>
                <hr>
                <el-row>
                  <el-button>微信登录</el-button>
                  <!--<el-button @click="forgetPwd()">忘记密码</el-button>-->
                </el-row>
              </el-form>
            </el-tab-pane>

            <el-tab-pane label="用户名登录">
              <el-form :model="loginRule" status-icon :rules="loginRules" ref="loginRule" label-width="">
                <el-form-item>
                  <el-col :span="12">
                    <el-select v-model="userClass" placeholder="个人用户" class="select_width">
                      <el-option
                        v-for="item in users"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-col>
                </el-form-item>
                <el-form-item label="" prop="userName">
                  <el-input type="text" v-model="loginRule.userName" autocomplete="off" placeholder="手机号/用户名/邮箱"></el-input>
                </el-form-item>
                <el-form-item label="" prop="graphCode">
                  <el-row :gutter="10">
                    <el-col :span="12">
                      <el-input style="" type="text" v-model="loginRule.graphCode" autocomplete="off"
                                placeholder="请输入图形验证码"></el-input>
                    </el-col>
                    <el-col :span="8">
                      <!--<el-button class="img_code" @click="getImgCode()">获取图形码</el-button>-->
                      <div v-html="svg" v-show="!isShow" class="img_code_height">
                        {{svg}}
                      </div>
                    </el-col>
                    <el-col :span="4">
                      <span class="refresh_img_code" @click="refreshImgCode()">换一换</span>
                    </el-col>
                  </el-row>
                </el-form-item>
                <el-form-item label="" prop="pass">
                  <el-input type="password" v-model="loginRule.pass" autocomplete="off" placeholder="请输入密码"></el-input>
                  <span class="forget_pwd" @click="forgetPwd()">忘记密码</span>
                </el-form-item>

                <el-form-item>
                  <el-button class="login_btn" type="primary" @click="userPwdForm()">登录</el-button>
                </el-form-item>
              </el-form>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-main>
    </el-container>
    <login-footer></login-footer>
  </div>

</template>

<script>
  import VerifyUtils from '../utils/utils'
  import LoginHeader from '../components/LoginHeader'
  import LoginFooter from '../components/LoginFooter'
  export default {
    name: "Login",
    components: {'login-header': LoginHeader, 'login-footer': LoginFooter},
    data() {
      var graphCode = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入图形验证码'));
        } else {
          callback();
        }
      };
      var messageCode = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入短信验证码'));
        } else {
          // if (VerifyUtils.phoneNumberVerify) {
          // this.$refs.loginRule.graphCode('graphCode');
          // }
          callback();
        }
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          // if (this.loginRule.checkPass !== '') {
          //   this.$refs.loginRule.validateField('checkPass');
          // }
          callback();
        }
      };
      var usernName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入用户名'));
        } else {
          callback();
        }
      };
      return {
        svg:'',
        isShow: false,
        users: [{
          value: 'personal',
          label: '个人用户'
        },
          {
            value: 'enterprise',
            label: '企业用户'
          }
        ],
        userClass: 'personal',
        loginRule: {
          phoneNumber: '',
          graphCode: '',
          messageCode: '',
          pass: '',
        },
        loginRules: {
          phoneNumber: [
            {validator: VerifyUtils.phoneNumberVerify, trigger: 'blur'}
          ],
          //图形码
          graphCode: [
            {validator: graphCode, trigger: 'blur'}
          ],
          //短信验证码
          messageCode: [
            {validator: messageCode, trigger: 'blur'}
          ],
          //密码
          pass: [
            {validator: validatePass, trigger: 'blur'}
          ],
          //用户名
          userName: [
            {validator: usernName, trigger: 'blur'}
          ],
        },
      }
    },
    created(){
      localStorage.clear();
      this.getImgCode();
    },
    mounted() {},
    methods: {
      submitForm() {
        console.log(this.loginRule+JSON.stringify(this.loginRule));
        this.$post('api/shortcutLogin', {
          captcha: this.loginRule.graphCode,
          mobile: this.loginRule.phoneNumber,
          smsCode:this.loginRule.messageCode.toString()
          // verifyCode:this.loginRule.messageCode.toString()
        }).then((res) => {
          // alert("123")
          console.log("login" + JSON.stringify(res));
          if (JSON.stringify(res.data.code) == 200){
            let userInfo = res.data.data;
            localStorage.setItem('userId',userInfo.accountId);
            // localStorage.setItem("userId",userInfo.id);//id
            if (VerifyUtils.validateIsNull(userInfo.nickname)){
              localStorage.setItem("nickname",userInfo.nickname);
            }
            if (VerifyUtils.validateIsNull(userInfo.name)){
              localStorage.setItem("name",userInfo.name);
            }
            if (VerifyUtils.validateIsNull(userInfo.phoneNumber)){
              localStorage.setItem("phoneNumber",userInfo.phoneNumber);
            }
            if (VerifyUtils.validateIsNull(userInfo.wechat)){
              localStorage.setItem("weChat",userInfo.wechat);
            }
            if (VerifyUtils.validateIsNull(userInfo.email)){
              localStorage.setItem("email",userInfo.email);
            }
            if (VerifyUtils.validateIsNull(userInfo.birthday)){
              localStorage.setItem("birthday",userInfo.birthday);
            }
            localStorage.setItem("gender",userInfo.gender);//性别
//头像
            localStorage.setItem("avatar",userInfo.avatar);

            this.$router.push({path: '/IndexPage'});
            localStorage.setItem("loginStatus", "1");
          }else{
            alert(res.data.msg);
          }
        })
        // this.$refs[formName].validate((valid) => {
        //   if (valid) {
        //     alert('submit!');
        //   } else {
        //     console.log('error submit!!');
        //     return false;
        //   }
        // });
      },
      //用户名密码登录
      userPwdForm(){
        localStorage.clear();
        var data = {
          captcha: this.loginRule.graphCode,
          mobile: this.loginRule.userName,
          accountType:this.userClass,
          password:this.$md5(this.loginRule.pass)
        }
        this.$post('/api/login',data).then((res) => {
          console.log("/api/login==" + JSON.stringify(res));
          if (JSON.stringify(res.data.code) == 200){
            console.log(JSON.stringify(res.data.data));
            let userInfo = res.data.data;
            //个人用户

            // debugger
            if (userInfo.type === "personal") {
              localStorage.setItem("userId",userInfo.Personal.accountId);//id
              localStorage.setItem("nickname",userInfo.Personal.nickname);
              localStorage.setItem("gender",userInfo.Personal.gender);//性别
              localStorage.setItem("name",userInfo.Personal.name);
              localStorage.setItem("email",userInfo.Personal.email);
              localStorage.setItem("weChat",userInfo.Personal.wechat);
              localStorage.setItem("birthday",userInfo.Personal.birthday);
              localStorage.setItem("phoneNumber",userInfo.Personal.phoneNumber);
              localStorage.setItem("avatar",userInfo.Personal.avatarFileId);
            }
            //企业用户
            if (userInfo.type === "enterprise") {
              localStorage.setItem("enterpriseId",userInfo.Enterprise.accountId);//id
              // {"enterpriseId":2,"name":"神州","email":"3123100","phoneNumber":"31233213"}}
              localStorage.setItem("name",userInfo.Enterprise.name);
              localStorage.setItem("email",userInfo.Enterprise.email);
              localStorage.setItem("weChat",userInfo.Enterprise.wechat);
              localStorage.setItem("contact",userInfo.Enterprise.contact);//联系人
              localStorage.setItem("phoneNumber",userInfo.Enterprise.phoneNumber);

            }
            localStorage.setItem("loginStatus", "1");//登录状态 1：登录 0：未登录
            // console.log("login=="+JSON.stringify(res.data));
            localStorage.setItem("CookieName",res.config.xsrfHeaderName);
            this.$router.push({path: '/IndexPage'});

          }else{
            alert(res.data.msg);
          }
          // alert("loginUser")
          console.log("loginUser" + JSON.stringify(res));
        })
      },
      getImgCode() {
        this.$nosignget('/api/service/captcha', {
          width: '',
          height: '',
        },).then(res => {
          if (res.status == 200) {
            this.svg = res.data;
            this.isShow = false;
          } else {
            alert(res.statusText);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      refreshImgCode() {
        this.getImgCode();
      },
      getMessageCode() {
        this.$get('/api/service/sms', {
          captcha: this.loginRule.graphCode,
          mobile: this.loginRule.phoneNumber,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            this.$message({
              message: '已发送短信验证码，请查收',
              type: 'success'
            });
            // console.log("test" + JSON.stringify(res.data.data));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      forgetPwd() {
        this.$router.push({path: '/ForgetPwd'})
      }
    }
  }
</script>

<style scoped>

  .login_div {
    /*为了固定底部的footer*/
    padding-bottom: 154px;
    position: relative;
  }

  .el-container {
    background-color: white;
  }

  .el-main {
    padding: 0;
  }

  .bg_img {
    background: url('../assets/bg_login@2x.png');
    background-size: auto 100%;
    backgroundRepeat: "no-repeat";
    height: 519px;
    margin-top: 20px;
    /*margin-left: -20px;*/
    /*margin-right: -20px;*/
  }

  .login_box {
    width: 350px;
    margin-right: 15rem;
    margin-top: 1rem;
    float: right;
  }

  .el-tabs__item {
    width: 176px;

  }
  .el-tabs--border-card > .el-tabs__header .el-tabs__item {
    border-top: 1px solid #33B2E8;
  }

  .login_btn {
    width: 306px;
    height: 43px;
    background-color: #33B2E8;
    text-align: center;
  }

  .img_code_height {
    height: 40px;
  }
  .refresh_img_code {
    font-size: 10px;
    line-height: 12px;
    color: red;
  }
  /*选择框的宽度*/
  .select_width{
    width: 320px;
  }
  /*提示登录语*/
  .hind_login{
    width:306px;
    height:20px;
    background:rgba(190,233,250,1);
    border:1px solid rgba(51,178,232,1);
    color: #33B2E8;
    font-size: 12px;
    margin-bottom: 10px;
  }
  .forget_pwd{
    font-size: 13px;
    color: #33B2E8;
    float: right;
  }
</style>



