<template>
  <div>
    <div class="bg_color_gray">
      <el-row :gutter="30" class="margin_left0" style="margin-left: 0px">
        <el-col :span="13" class="white_bg_color">
          <el-row :gutter="10" class="text_align_left">
            <el-col :span="5" class="user_img">
              <img src="../../assets/personal/touxiang_bg@2x.png" height="90" width="90"/>
            </el-col>
            <el-col :span="19">
              <p class="personal_info">{{userName}}</p>
              <p class="personal_info">欢迎您加入爱惠康～</p>
              <p class="personal_info" v-model="tel">{{tel}}&emsp;&emsp;&emsp;<span class="change_tel" @click="modifyTel()">更换</span></p>
              <p class="change_personal_info" @click="modifyPersonData()">修改个人资料>></p>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="1">
          &emsp;
        </el-col>
        <el-col :span="9" class="white_bg_color height163">
          <el-row class="text_align_left">
            <el-col :span="12">
              <p><span class="remaining_sum">{{accountBalance}}</span><span class="yuan">元</span></p>
              <img class="coupon_img" src="../../assets/personal/zhanghuyue_icon@2x.png" height="28" width="28">
              <span>账户余额</span>
            </el-col>
            <el-col :span="12">
              <p><span class="coupon_sheet">{{coupons}}</span><span class="yuan">张</span></p>
              <img class="coupon_img" src="../../assets/personal/youhuiquan_icon@2x.png" height="28" width="28"/>
              <span>优惠券</span>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
      <div style="margin-top: 10px">
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect"
                 active-text-color="#33B2E8">
          <el-menu-item index="1">全部订单</el-menu-item>
          <el-menu-item index="2">未付款</el-menu-item>
          <el-menu-item index="3">未使用</el-menu-item>
        </el-menu>
        <div class="order_div">

          <img src="../../assets/personal/wudingdan_icon@2x.png" height="88" width="" style="margin-top: 20px"/>
          <p>暂时没有相关订单</p>
        </div>
        <!--<compent :is="modelCode"></compent>-->
      </div>
      <div style="margin-top: 10px">
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect"
                 active-text-color="#33B2E8">
          <el-menu-item index="1">优惠券</el-menu-item>
        </el-menu>
        <div class="coupon_div">
          <el-row :gutter="50">
            <el-col :span="2">
              &emsp;
            </el-col>
            <el-col :span="5">
              <img src="../../assets/personal/yhq_img1@2x.png" height="177" width=""/>
            </el-col>
            <el-col :span="5">
              <img src="../../assets/personal/yhq_img1@2x.png" height="177" width=""/>
            </el-col>
            <el-col :span="5">
              <img src="../../assets/personal/yhq_img1@2x.png" height="177" width=""/>
            </el-col>
            <el-col :span="5">
              <img src="../../assets/personal/yhq_img1@2x.png" height="177" width=""/>
            </el-col>
            <el-col :span="2">
              &emsp;
            </el-col>
          </el-row>
        </div>
        <!--<compent :is="modelCode"></compent>-->
      </div>
    </div>
  </div>

</template>

<script>
  import {mapGetters} from 'vuex';
  import VerifyUtils from '../../utils/utils';
  export default {
    props: ['selectTitle'],
    data() {
      return {
        userName:'',
        tel: '',
        activeIndex: '1',
        accountBalance:0.00,
        coupons:0
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },
    created(){
      this.getPersonInfo();
    },
    methods: {
      //修改个人手机号
      modifyTel(){
        this.$router.push({ path: '/SafeSetting'});
      },
      //修改个人资料
      modifyPersonData(){
        this.$router.push({ path: '/PersonalData'});
      },
      getPersonInfo(){
        let userId = localStorage.getItem("userId");
        let userInfo = '/api/personal/'+ userId;
        this.$get(userInfo, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            if (VerifyUtils.validateIsNull(res.data.data.name)){
              // this.userName = localStorage.getItem("nickname");
              this.userName  = res.data.data.name;
            }
            if (VerifyUtils.validateIsNull(res.data.data.Account.phoneNumber)){
              this.tel = res.data.data.Account.phoneNumber;
              // this.tel = localStorage.getItem("phoneNumber");
            }
            // console.log("PersonalPage==" + JSON.stringify(res.data.data));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<style>
  @import "../../styles/personal/personal-center.scss";

  .height163 {
    height: 163px;
  }
  .margin_left0 {
    margin-left: 0px;
  }

  /*个人信息*/
  .text_align_left {
    text-align: left;
  }

  .user_img {
    text-align: center;
    margin-top: 20px;
  }

  .personal_info {
    color: #404040;
    font-size: 14px;
  }

  .change_tel {
    color: #33B2E8;
    font-size: 14px;
  }

  .change_personal_info {
    float: right;
    color: #33B2E8;
    font-size: 14px;
  }

  /*账户余额*/
  .remaining_sum {
    color: #33B2E8;
    font-size: 30px;
  }

  .yuan {
    font-size: 14px;
    color: #666666;
  }

  .coupon_sheet {
    color: #FA6A5A;
    font-size: 30px;
  }

  .coupon_img {
    vertical-align: middle
  }
  .order_div{
    height: 300px;background-color: white;text-align: center;
  }
  .coupon_div{
    height: auto;
    background-color: white;
  }
</style>






