<template>
  <div>
    <!--标题-->
    <el-row>
      <el-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4">
        <div>
          &emsp;
        </div>
      </el-col>
      <el-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
        <div class="float_left recommendTitle">
          推荐套餐
        </div>
      </el-col>
      <el-col :xs="7" :sm="7" :md="7" :lg="7" :xl="7">
        <div class="float_right recommendMore">
          查看更多>
        </div>
      </el-col>
      <el-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5">
        <div>
          &emsp;
        </div>
      </el-col>
    </el-row>
    <!--布局-->
    <el-row :gutter="10">
      <el-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4">
        <div>
          &emsp;
        </div>
      </el-col>
      <el-col :xs="5" :sm="5" :md="5" :lg="4" :xl="4">
        <div class="float_left">
          <img src="../assets/checkUp/taocan_banner1@2x.png" height="394" width=""/></div>
      </el-col>
      <el-col :xs="11" :sm="11" :md="11" :lg="11" :xl="11">
        <el-row :gutter="20">
          <el-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8" v-for="(p, index) in productData" :key="index" :class="{'marginTop10':index>2}">
            <el-card :body-style="{ padding: '0px' }" @click="productDetail(p.CatalogProduct.id)">
              <img :src=p.imgUrl class="goods_image"  @click="productDetail(p.CatalogProduct.id)">
              <div class="card_content">
                <p class="card_title">{{p.CatalogProduct.name}}</p>
                <span class="card_subtitle">{{p.CatalogProduct.simpleDescription}}</span>
                <p class="margin_top0_bottom0"><span class="discount_price">¥</span><span
                  class="discount_price">{{p.CatalogProduct.salePrice}}</span><span class="origin_price">起</span>&emsp;&emsp;&emsp;<span
                  class="origin_price line" v-if="p.CatalogProduct.marketPrice !== null">¥</span><span class="origin_price line">{{p.CatalogProduct.marketPrice}}</span></p>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </el-col>
      <el-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4">
        <div>
          &emsp;
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import Verify from '../utils/utils';
  export default {
    name: "HealthCheckup",
    data() {
      return {
        total:0,
        productData:[],
      }
    },
    created() {
      this.getProductList();
    },
    methods: {
      getProductList() {
        // let userId = localStorage.getItem("userId");
        // let product = '/api/user/products';
        let product = '/api/ad/products/indexMedicalExaminationPackage';
        this.$get(product, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("test" + JSON.stringify(res.data.data));
            this.productData = res.data.data;
            for (let i=0;i<this.productData.length;i++){
              if (Verify.validateIsNull(this.productData[i].CatalogProduct.File)) {
                this.productData[i]['imgUrl'] = "http://"+this.productData[i].CatalogProduct.File.url;
              }
            }

          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      productDetail(id){
        debugger
        console.log("111="+id);
        this.$router.push({ path: '/MedicalProductDetail',query:{productId:id}})
      }
    }
  }
</script>

<style scoped>
  @import "../styles/personal/personal-center.scss";

  /*套餐title*/
  .recommendTitle {
    font-size: 22px;
    color: #333333;
  }

  .recommendMore {
    font-size: 14px;
    color: #666666;
  }

  /*套餐的布局*/
  .marginTop10 {
    margin-top: 20px;
  }
  .goods_image{
    width:280px;
    height:160px;
  }

  /*.card_content {*/
  /*text-align: left;*/
  /*padding-left: 10px;*/
  /*}*/
  /*.card_title {*/
  /*color: #404040;*/
  /*font-size: 17px;*/
  /*margin-bottom: 0px;*/
  /*margin-top: 10px;*/
  /*}*/
  /*.card_subtitle {*/
  /*font-size: 15px;*/
  /*color: #A2A2A2;*/
  /*}*/
  /*.discount_price{*/
  /*font-size: 25px;*/
  /*color: #DF2321;*/
  /*}*/
  /*.origin_price{*/
  /*font-size: 15px;*/
  /*color: #A2A2A2;*/
  /*}*/
  /*.line{*/
  /*text-decoration:line-through;*/
  /*}*/
  /*.margin_top0_bottom0{*/
  /*margin-top: 0px;*/
  /*margin-bottom: 0px;*/
  /*}*/
  /*.image {*/
  /*width: 100%;*/
  /*display: block;*/
  /*}*/

</style>



