<template>
  <div class="padding_left_right">
    <el-row :gutter="20">
      <el-col :span="18" class="margin_top15" v-if="!detailFlag">
        <div class="white_bg_color">
          <el-row v-for="(article,index) in articleList" :key="index">
            <el-col :span="24" class="text_align_left padding_left10 padding_right10">
              <el-row>
                <el-col :span="16">
                  <p class="advice_title">{{article.name}}</p>
                </el-col>
                <el-col :span="8" class="margin_top15 text_align_right padding_right20">
                  <img src="../../assets/healthAdvice/shijian@2x.png" height="16" width="16" class="vertical_align"/>
                  <span class="date_font">{{article.updatedAt}}</span>
                </el-col>
              </el-row>

              <el-row v-if="article.imgUrl &&  article.hasOwnProperty('imgUrl')">
                <el-col :span="5">
                  <img :src="article.imgUrl" width="120px" height="100px"/>
                </el-col>
                <el-col :span="19">
                  <p class="article_content">{{article.summary}}</p>
                  <!--<p class="article_content">{{article.content}}</p>-->
                </el-col>
              </el-row>
              <!--<p v-else class="article_content">{{article.content}}</p>-->
              <p v-else class="article_content">{{article.summary}}</p>
              <el-row>
                <el-col :span="6">
                  <img class="vertical_align" src="../../assets/healthAdvice/biaoqian@2x.png" height="16" width="16"/>
                  <span class="date_font" v-for="(l,index) in article.tags" :key="index">
                    {{l.tag}}
                  </span>
                </el-col>
                <el-col :span="3">
                  <img class="vertical_align" src="../../assets/healthAdvice/yanjing@2x.png" height="16" width="16"/>
                  <span class="date_font">{{article.pageviews}}</span>
                </el-col>
                <el-col :span="15" class="text_align_right padding_right20">
                  <el-button class="all_content_font" type="text" @click.native="readContent(article.id)">阅读全文</el-button>
                </el-col>
              </el-row>
              <hr class="separate"/>
            </el-col>
          </el-row>
        </div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="10"
          layout="total, prev, pager, next"
          :total= articleListTotal>
        </el-pagination>
      </el-col>
      <!--体检详情-->
      <el-col :span="18" class="margin_top15" v-if="detailFlag">
        <article-detail></article-detail>
      </el-col>

      <el-col :span="6">
        <div class="white_bg_color">
          <p class="label_title">标签</p>
          <label v-for="l in labelList" class="label_font"><span class="padding_right10">{{l.name}}</span></label>
        </div>
        <div class="white_bg_color margin_top15">
          <p class="label_title">热门文章</p>
          <p class="label_font text_align_left padding_left10" v-for="a in hotArticle">
            {{a.title}}
          </p>
          <el-pagination
            layout="prev, pager, next"
            :total= hotArticleTotal>
          </el-pagination>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import Verify from '../../utils/utils';
  import ArticleDetail from '../../components/ArticleDetail'
  import {mapGetters} from 'vuex'

  export default {

    data() {
      return {
        articleList: [],
        currentPage:1,
        articleListTotal:0,
        // tagArr:[],
        labelList: [],
        hotArticle: [],
        hotArticleTotal:0,
        detailFlag:false,
        articleTitle:'',//文章标题
        articleCovers:'',//文章封面
        articleContent:'',//文章内容
        publishedTime:'',//这里取的最后一次修改时间
        articleViews:'',//文章浏览量
        articleId:'',
      }
    },
    components:{
      ArticleDetail,
    },
    created() {
      //获取文章列表
      this.getArticleList();
      //获取标签列表
      this.getTagsList();
      //获取热门文章
      this.getHotArticle();
      this.getArticleDetail();
    },
    methods: {
      getArticleDetail() {
        let _self = this;
        // let url = '/api/content/articles/'+this.$route.query.id;
        // debugger
        let url = '/api/content/articles/'+15;
        _self.$get(url, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("文章详情==" + JSON.stringify(res.data.data));
            _self.articleTitle = res.data.data.title;
            if (res.data.data.File !=null) {
              _self.articleCovers = "http://"+res.data.data.File.url;
            }
            _self.publishedTime = Verify.convertDate(res.data.data.updatedAt);
            _self.articleViews = res.data.data.pageviews;
            _self.articleContent = res.data.data.summary;//content
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      getTagsList() {
        let _self = this;
        //获取Label列表
        _self.$get('/api/content/tags', {
          displayOrder: 'desc',//排序方式 (默认 ：desc) 或 asc
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            // console.log("label列表" + JSON.stringify(res.data.data));
            let arr = res.data.data;
            for (let i = 0; i < arr.length; i++) {
              const obj = {
                id: arr[i].id,
                name: arr[i].name
              };
              _self.labelList.push(obj);
            }
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      getArticleList() {
        let _self = this;
        //获取Label列表
        _self.$get('/api/content/articles', {
          page: _self.currentPage,
          perPage: 10,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            let arr = res.data.data.items;
            _self.articleListTotal = arr.length;
            for (let i = 0; i < arr.length; i++) {
              let tagMap = arr[i].ArticleTagMappings;
              let tagArr = [];
              for (var j = 0; j < tagMap.length; j++) {
                const o = {
                  id: tagMap[j].Tag.id,
                  tag: tagMap[j].Tag.name,
                };
                tagArr.push(o);
              }
              // console.log("arr[i]==" + JSON.stringify(res.data.data));
              let imgUrl = '';
              if (arr[i].File == null) {
                imgUrl = '';
              } else {
                imgUrl = 'http://' + arr[i].File.url;
              }
              const obj = {
                id: arr[i].id,
                name: arr[i].title,
                summary: arr[i].summary,
                content: arr[i].content,
                updatedAt: Verify.convertDate(arr[i].updatedAt),
                tags: tagArr,
                pageviews: arr[i].pageviews,
                imgUrl: imgUrl
              };
              _self.articleList.push(obj);
            }
            console.log("文章==" + JSON.stringify(_self.articleList));

          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      getHotArticle(){
        let _self = this;
        //获取Label列表
        _self.$get('/api/content/hotarticles', {
          page: 1,
          perPage: 10,
          // tagId:'',
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            // console.log("热门文章列表" + JSON.stringify(res.data.data.items));
            let arr = res.data.data.items;
            _self.hotArticleTotal = arr.length;
            for (let i = 0; i < arr.length; i++) {
              const obj = {
                id: arr[i].id,
                title: arr[i].title
              };
              _self.hotArticle.push(obj);
            }
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      readContent(id){
        this.detailFlag = true;
        this.$store.dispatch('setArticleId',id);
        // setArticleId
        // this.$router.push({ path: '/ArticleDetail',query:id});
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.currentPage = val;
        this.getArticleList();
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/personal/personal-center.scss";

  .padding_left_right {
    padding-left: 10%;
    padding-right: 10%;
    background-color: #F6F6F6;
  }

  .advice_title {
    font-size: 18px;
    font-weight: bold;
    color: #404044;
  }

  .label_title {
    font-size: 14px;
    font-weight: bold;
    color: #666666;
    text-align: left;
    padding-left: 10px;
    padding-top: 10px;
  }

  .date_font {
    color: #CCCCCC;
    font-size: 12px;
  }

  .all_content_font {
    color: #33b2e8;
    font-size: 12px;
  }

  .vertical_align {
    vertical-align: middle;
  }

  .article_content {
    font-size: 12px;
    color: #333333;
    line-height: 24px;
  }

  /*文章分割线*/
  .separate {
    height: 2px;
    background-color: #e6e6e6;
    border: none;
  }

  .label_font {
    font-size: 16px;
    color: #333333;
  }
  .class1{
    display: none;
  }
  .class2{
    display: block;
  }
</style>



