<template>
  <el-header>
    <el-row>
      <el-col :span="12">
        <div class="logo_img_div">
          <img src="../assets/logo_login@2x.png" class="logo_img"/>
        </div>
      </el-col>
      <el-col :span="12">
        <el-form ref="form" :model="form" class="search_form">
          <el-input v-model="form.name" placeholder="请输入关键词"
                    suffix-icon="el-icon-search"></el-input>
        </el-form>
      </el-col>
    </el-row>


  </el-header>
</template>

<script>
  export default {
    name: "HomeHeader",
    data(){
      return{
        form:{
          name:''
        },
      }
    }
  }
</script>

<style scoped>
  .el-row{
    background-color: white;
    margin-left: -20px;
    margin-right: -20px;
  }
  .logo_img_div {
    margin-left: 6.75rem;
    text-align: left;
  }

  .logo_img {
    text-align: left;
    height: 60px;
    margin-top: 0.75rem;
  }
  .search_form{
    width:420px;
    height:49px;
    margin-top: 24px;
  }
</style>



